<template>
  <div class="max-w-xs rounded overflow-hidden shadow-lg bg-white flex flex-col justify-between">
    <img class="w-full h-48 object-cover" :src="value.image" alt="Sunset in the mountains">
    <div class="px-6 py-4">
      <div class="font-bold text-gray-600 text-xl">{{ value.name }}</div>
      <div class="font-bold text-sm text-gray-800 mb-2">{{ value.price | money }}</div>
      <p class="text-gray-700 text-sm">
        {{ value.description }}
      </p>
    </div>
    <div class="px-6 py-4">
      <a @click="addToCart(value)" href="javascript:void(0);" class="inline-block bg-gray-200 hover:bg-gray-400 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 w-full text-center">Add to cart</a>
    </div>
  </div>
</template>

<script>
  import { mapActions } from 'vuex'

  export default {
    name: 'Product.vue',

    props: {
      value: {
        type: Object
      }
    },

    methods: {
      ...mapActions(['addToCart'])
    }

  }
</script>
